  margin-right: -8px;
}
.component-one button {
  font-family: "Roboto", Bold, sans-serif;
  font-weight: 700;
  padding: 8px 20px;
  font-size: 36px;
  line-height: 42px;
  letter-spacing: -0.05em;
  color: var(--anotherSky);
  background: var(--winterSky);
  border-radius: 10px;
  border: none;
}
.component-one img {
